.MatchDetailCard{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 1rem;
    margin-bottom: 2rem;
    .match-date, .match-venue, .match-result{
        margin-top: 1rem;
    }

    .additional-detail{
        text-align: right;
        & > p{
            margin-bottom: 2rem;
        }
    }
}