.TeamPage{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;

    .team-name-section{
        grid-column: span 3;
        justify-self: start;
        align-self: end;
    }

    .team-name{
        font-size: 4rem;
        margin-bottom: 2rem;
    }

    .win-loss-section{
        justify-self: center;
        padding: 3rem;
        text-align: center;
    }

    .match-detail-section{
        grid-column: span 4;
    }

    .more-link{
        align-self: center;
        justify-self: center;
        font-size: 1.5rem;
    }
}